var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-app',{attrs:{"id":"inspire"}},[_c('header',{attrs:{"role":"banner"}},[_c('div',{staticClass:"secw-narrow"},[_c('div',{attrs:{"id":"header-logo"}},[_c('router-link',{attrs:{"to":"/"}},[_c('img',{staticClass:"img-block",attrs:{"alt":"","src":"assets/i/logo.svg"}})])],1)])]),_c('v-main',{staticStyle:{"margin-bottom":"50px"}},[_c('v-container',{attrs:{"grid-list-md":""}},[_c('v-layout',{attrs:{"justify-center":"","row":"","wrap":""}},[_c('v-flex',{staticStyle:{"text-align":"center"},attrs:{"md12":""}},[_c('a',{on:{"click":function($event){_vm.dialog = true}}},[_c('img',{attrs:{"src":"assets/i/manifest.png","width":"60%"}})]),_c('v-card-text',[_c('p',{staticClass:"text"},[_vm._v(" The physical autonomy of robots is well understood both theoretically and practically. By contrast, there is almost no research exploring their potential economic autonomy. In this paper, we present the first economically autonomous robot -- a robot able to produce marketable goods while having full control over the use of its generated income. Gaka-chu (\"painter\" in Japanese) is a 6-axis robot arm that creates artistic paintings of Japanese kanjis from an autoselected keyword. By using a blockchain-based smart contract, Gaka-chu can autonomously list a painting it made for sale in an online auction. In this transaction, the robot interacts with the human bidders as a peer not as a tool. Using the blockchain-based smart contract, Gaka-chu can then use its income from selling paintings to replenish its resources by autonomously ordering materials from an online art shop, and maintain its activity by remunerating a human assistant for manual tasks such as placing canvases. We built the Gaka-chu prototype with an Ethereum-based smart contract and ran a 6-month long experiment, during which the robot painted and sold four paintings one-by-one, simultaneously using its income to purchase art suppl